<template>
    <el-container>
        <el-form :model="loginForm" status-icon :rules="log" ref="loginForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名" prop="userName">
                <el-input v-model="loginForm.userName"></el-input>
            </el-form-item>

            <el-form-item label="密码" prop="pass">
                <el-input type="password" v-model="loginForm.pass" autocomplete="off"></el-input>
            </el-form-item>

            <el-link type="warning" :underline="false">忘记密码？</el-link>
            <el-link type="primary" :underline="false">还没账号？快来注册吧</el-link>
            <el-form-item>
                <el-button type="primary" @click="login">提交</el-button>
                <el-button @click="resetForm('loginForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </el-container>
</template>

<script>
    import { mapMutations } from 'vuex';   //引入vuex并解构赋值

    export default {
        name: 'Login',
        data() {
            return {
                //用户名与输入框绑定的值
                loginForm: {
                    pass: '',
                    checkPass: '',
                    age: '',
                    userName:''
                },
                //验证器验证规则
                log: {
                    pass: [
                        { required:true, validator: this.validators.validateLoginPass, trigger: 'blur'}
                    ],
                    userName: [
                        { required:true, validator: this.validators.validateuserName, trigger: 'blur'}
                    ],
                    age: [
                        { required:true, validator:  this.validators.checkAge, trigger: 'blur'}
                    ]
                }
            };
        },
        //在事件中使用...引入vuex
        methods: {
            ...mapMutations(['$_setToken']),
                login: function () {
                let _this = this;
                if (this.loginForm.userName === "" || this.loginForm.pass === "") {
                    _this.$message.error("请输入手机号或密码");
                } else {
                    this.$axios({
                        method: 'post',
                        url: '/login',
                        data: _this.loginForm
                    }).then((res) => {
                        console.log(res.data);

                        this.$message({
                            message: '登陆成功',
                            type: 'success'
                        })
                        this.$router.push({name: 'my'})

                    }).catch(function (err) {
                        console.log(err);
                        _this.$message.error("登录错误，请联系程序开发人员！！");

                    })
                }
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }

</script>